import { HttpService } from './../../../Services/http/http.service';
import { Component, OnInit } from '@angular/core';
import { AbstractControl, FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AppUserTypeService } from 'src/app/demo/Services/AppUserType/appUserType.service';
import { AppUserTypeDto } from 'src/app/demo/_model/appUserType';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { Nationality } from 'src/app/demo/_model/nationality';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { GovernorateReDto } from 'src/app/demo/_model/governorate';

import { SubscribeType } from 'src/app/demo/_model/subscribeType';
import { AccountService } from 'src/app/demo/Services/account/account.service';
import { TimeRe } from 'src/app/demo/_model/timeRe';
import { StudentTimeAdd } from 'src/app/demo/_model/_Student/studentTime';
import { StudentAdd } from 'src/app/demo/_model/_Student/student';
import { IOption } from 'ng-select';
import { ToastrService } from 'ngx-toastr';



@Component({
  selector: 'app-auth-signup-v2',
  templateUrl: './auth-signup-v2.component.html',
  styleUrls: ['./auth-signup-v2.component.scss']
})
export class AuthSignupV2Component implements OnInit {
  RegisterForm: FormGroup;
  Nationalities: Array<IOption>;
  Governorates: Array<IOption>;
  Times: Array<IOption>;
  studentTimes: StudentTimeAdd[];
  selectedValue: string;
  NationalityService: BaseService<Nationality>;
  Subscribes: SubscribeType[];
  GovernorateService: BaseService<GovernorateReDto>;
  TimeService: BaseService<TimeRe>;
  subscribeService: BaseService<SubscribeType>
  constructor(private formBuilder: FormBuilder, private toastr: ToastrService, private AccountService: AccountControllerService, private httpSv: HttpService, private account: AccountService) {
    this.NationalityService = new BaseService<Nationality>(httpSv, ApiPath.Nationality);
    this.subscribeService = new BaseService<SubscribeType>(httpSv, ApiPath.SubscribeType)
    this.GovernorateService = new BaseService<GovernorateReDto>(httpSv, ApiPath.Governorate);
    this.TimeService = new BaseService<TimeRe>(httpSv, ApiPath.Time);
  }

  ngOnInit() {
    this.NationalityService.GetAll().subscribe((res: Nationality[]) => {
      const data = res.map((x) => {
        const temp = {
          label: x.name,
          value: x.id.toString()
        };
        return temp;
      });

      this.Nationalities = data;
      console.log(this.Nationalities);
    });
   
    this.subscribeService.GetAll().subscribe(res=>{
      this.Subscribes = res;
    })
    this.GovernorateService.GetAll().subscribe((res: GovernorateReDto[]) => {
      const data = res.map((x) => {
        const temp = {
          label: x.name_ar,
          value: x.id.toString()
        };
        return temp;
      });
      this.Governorates = data;
      console.log(this.Governorates);
    });

    this.TimeService.GetAll().subscribe((res: TimeRe[]) => {
      const data = res.map((x) => {
        const temp = {
          label: x.dayName,
          value: x.id.toString()
        };
        return temp;
      });
      this.Times = data;
      console.log(this.Times);
    });
    this.initForm();
  }
  initForm() {
    this.RegisterForm = this.formBuilder.group({
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      email: ['', Validators.required],
      age: ['', Validators.required],
      mobileNumber: ['', Validators.required],
      insteadMobileNumber: [],
      nationalityId: ['', Validators.required],
      subscribeId: [],
      governorateId: [],
      savedParts: ['', Validators.required],
      password: ['', Validators.required, Validators.minLength(6), Validators.maxLength(40)],
      confirmPassword: ['', Validators.required],
      studentTimes: ['', Validators.required],
    },
    );
  }
  get f(): { [key: string]: AbstractControl } {
    return this.RegisterForm.controls;
  }

  onFormSubmit(): void {
    let data: StudentAdd = this.RegisterForm.value;
    console.log(data);

    data.studentTimes = [];
    this.RegisterForm.controls?.studentTimes?.value.forEach((e) => {
      let up = {
        id: 0,
        timeId: e,
        studentId: 0,
        isDeleted: false
      };
      data.studentTimes.push(up);
    });
    this.AccountService.Add(data).subscribe((res) => {
      this.toastr.success("تم التسجيل بنجاح")
      this.account.setCurrentUser(res);
      this.account.getCurrentUser();
    }, error => {
      this.toastr.error("يرجي التحقق من جودة الاتصال بالانترنت")
    });
  }
}
